:root {
  --ss-bg-color: #10121e;
  --ss-border-color: #ffd500;
  --ss-primary-color: #ffd500;
  --ss-font-color: #fff;
}

body {
  color: #ffd500;
  background-color: #000;
  background-image: url("background-cat-min.f5a8b60e.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.cat-info {
  align-items: center;
  justify-items: center;
  margin: 40px 0;
  display: grid;
}

.cat-image {
  height: 400px;
  border-radius: 5px;
  box-shadow: 0 0 50px 15px #ffd500;
}

.cat-name, .cat-description, .cat-temperament {
  max-width: 500px;
  text-shadow: 4px 4px 15px rgba(0, 0, 0, .6);
}

.cat-name, .cat-span, .success, .error {
  text-transform: uppercase;
  color: #0ff;
}

.loader-container {
  width: 100vw;
  height: 50vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  animation: spin 4s linear infinite;
}

.success, .error {
  justify-content: center;
  display: flex;
  position: relative;
  top: -90px;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

